import React from "react"
import Navbar from "react-bootstrap/Navbar"
import { Nav } from "react-bootstrap"
// import logo from "../assets/images/logo.svg"
import { Link } from "gatsby"

class TopNavBar extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      bg: "transparent",
    }
  }

  isActive({ isCurrent }) {
    return isCurrent ? { className: "active" } : null
  }

  listenScrollEvent = e => {
    if (window.scrollY > 300) {
      this.setState({ bg: "dark" })
    } else {
      this.setState({ bg: "transparent" })
    }
  }

  componentDidMount() {
    window.addEventListener("scroll", this.listenScrollEvent)
  }

  render() {
    return (
      <Navbar collapseOnSelect expand="lg" bg={this.state.bg} fixed="top">
        <Navbar.Brand>
          <img
            alt=""
            src={"/images/logo.svg"}
            width="30"
            height="30"
            className="d-inline-block align-top"
          />{" "}
          Sameeha Rahman
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsiveNavbar" />
        <Navbar.Collapse id="responsiveNavbar" className="justify-content-end">
          <Link to="/" getProps={this.isActive} exact>
            <Nav.Item>Home</Nav.Item>
          </Link>
          <Link to="/projects" getProps={this.isActive}>
            <Nav.Item>Projects</Nav.Item>
          </Link>
          <Link to="/posts" getProps={this.isActive}>
            <Nav.Item>Blog Posts</Nav.Item>
          </Link>
        </Navbar.Collapse>
      </Navbar>
    )
  }
}

export default TopNavBar
